.navBar {
  display: flex;
  background-color: #00628b;
  align-items: center;
  
  height: 70px;
  justify-content: space-between;
}
.navitem {
  justify-content: center;
  font-family: cursive;
}
.nav-link {
  color: white;
}
.navitem:hover {
  transform: scale(1.2);
}
.brand {
  font-size: 30px;
  padding-left: 15px;
}
.navitem1 {
  display: flex;
}
.navitem2 {
  padding: 10px;
}
.navInput {
  border: none;
  height: 35px;
  border-radius: 5px 0px 0px 5px;
  font-family: "Shadows Into Light";
}
.navSubmit {
  height: 36px;
  border-radius: 0px 5px 5px 0px;
  border: none;
  background-color: #e6e6dc;
  font-family: "Shadows Into Light";
}
